.topbar-section {
  position: fixed;
  background: #01132f;
  padding: 7px 0 0px 0px;
  width: 100%;
  z-index: 11;
  top: 0;
}

.topbar-section::after {
  background-color: #fbb415;
  position: absolute;
  top: 0;
  bottom: -1px;
  right: -63px;
  width: 45%;
  content: "";
  transform: skew(-37deg);
  border-left: 10px solid #39475d;
  z-index: 0;
}

.topbar-section .topbar-inner {
  display: inline-block;
  width: 100%;
  padding: 0;
  vertical-align: middle;
}

.topbar-inner ul li {
  font-size: 14px;
  font-family: "Roboto";
  color: #fff;
  padding: 6px 25px 6px 0px;
  margin-right: 20px;
  margin-bottom: 0;
}

.top-bar-right ul li:last-child {
  padding-right: 0px;
  margin-right: 0px;
}
.view-more-cust {
  width: 100%;
}
.view-more-cust a {
  color: #fff !important;
}
.top-bar-left ul li,
.top-bar-right ul li {
  display: inline-block;
}

.top-bar-right ul li a {
  color: #01132f;
}

.topbar-inner .top-bar-left ul li i {
  font-size: 20px;
  background: #213451;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  padding: 5px;
