  display: inline-block;
  color: #fff;
  text-decoration: none;
  border-radius: 50px;
  border: 1px solid #fff;
  transition: all 0.5s ease;
  z-index: 0;
  transition: all 0.3s ease 0s;
  background: transparent;
}

.view-more1:hover,
.view-more1:active,
.view-more1:focus {
  color: #9bf7d1 !important;
}

.view-more::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  border-radius: 5px;
  transition: all 0.3s;
  display: none;
}

.view-more:hover,
.view-more:active,
.view-more:focus {
  color: #9bf7d1 !important;
}

.view-more::before {
  background-color: transparent;
  opacity: 0;
  transform: scale(0.5, 0.5);
}

.view-more:hover::before {
  opacity: 1;
  transform: scale(1, 1);
  background-color: rgb(255, 172, 0);
}

.view-more::after {
  display: none;
}

.view-more::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  border-radius: 5px;
  opacity: 1;
  transition: all 0.3s;
  border: 2px solid #fcbd30;
  transform: scale(1, 1);
}

.view-more:hover::after {
  opacity: 0;
  transform: scale(1.2, 1.2);
}

