.steps ul {
  overflow: hidden;
  margin: 0;
  padding: 1em 0;
  list-style: none;
  line-height: 1.2;
  font-size: 1em;
  counter-reset: li;
  animation: chase 1s 1 forwards;
}

.steps li {
  float: left;
  display: inline;
  position: relative;
  counter-increment: li;
  width: 11%;
  text-align: center;
  color: #ccc;
  transition: all 0.5s ease;
}

.steps li:before {
  content: "\f111";
  font: normal normal normal 22px/1 FontAwesome;
  position: relative;
  background-color: #9c9c9c;
  color: #7a7a7a;
  width: 35px;
  height: 35px;
  line-height: 35px;
  margin: 0 auto 0.5em;
  border-radius: 100%;
  font-size: 22px;
  text-align: center;
  display: block;
  z-index: 2;
}

.steps li:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 8px;
  display: block;
  background: #d9d9d9;
  z-index: 1;
  top: 14px;
  left: 50%;
}

.steps li:first-child.active:after {
  left: 50%;
}

.steps li:last-child:after {
  display: none;
}

.steps li.done {
  color: #3a8983;
}

.steps li.done:after {
  width: 100%;
  background-color: #3a8983;
  background-image: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0.35) 25%,
    transparent 25%,
    transparent 50%,
    rgba(0, 0, 0, 0.35) 50%,
    rgba(0, 0, 0, 0.35) 75%,
    transparent 75%,
    transparent
  );
  background-repeat: repeat-x;
  background-size: 12px 12px;
  animation: progress 0.3s linear 0;
}

.steps li.done:before {
  background-color: #3a8983;
  border-color: #3a8983;
  color: white;
  content: "\f00c";
  font: normal normal normal 14px/1 FontAwesome;
  line-height: 35px;
}

.steps li.active {
  font-weight: bold;
  color: #57aed1;
}

.steps li.active:after {
  width: 100%;
  animation: progress 0.3s linear infinite;
}

@keyframes chase {
  from {
    width: 80%;
  }

  to {
    width: 100%;
  }
}

