/*-----------------
1. General
-----------------------*/

html {
  height: 100%;
}

.search-sec .form-control::-webkit-input-placeholder {
  color: #fff !important;
}

.search-sec .form-control:-ms-input-placeholder {
  color: #fff !important;
}

.search-sec .form-control::placeholder {
  color: #fff !important;
}

p > span {
  font-family: "Montserrat", sans-serif !important;
  font-size: 14px !important;
}

body {
  background-color: #ffffff;
  color: #555;
  font-family: "Montserrat", sans-serif;
  font-size: 0.8rem;
  height: 100%;
  line-height: 1.5;
  overflow-x: hidden;
}

.discussion-form {
  box-shadow: 0 0 6px #ccc;
  padding: 12px;
  margin-bottom: 16px;
}

.discussion_btn {
  min-height: 0 !important;
  padding: 10px !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  line-height: 1.2;
  margin: 0;
  color: #333333;
}

.pointer {
  cursor: pointer;
}

a,
a:hover,
a:active,
a:focus {
  outline: none;
  text-decoration: none;
  color: inherit;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

input,
button,
a {
  transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
}

input,
input:focus,
button,
button:focus {
  outline: none;
  border-color: inherit;
}

p {
  margin-bottom: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  cursor: pointer;
}

/*-----------------
    2. Bootstrap Classes
    -----------------------*/

.sitename {
  padding-top: 25px;
  font-size: 22px;
  text-transform: uppercase;
  font-weight: 600;
  color: #de1818;
}

.sitename {
  animation: blinkMe 2s linear infinite;
}

@keyframes blinkMe {
  0% {
    opacity: 0.9;
    color: #de1818;
  }

  33% {
    opacity: 1;
    color: #182cde;
    text-decoration: underline;
  }

  60% {
    opacity: 1;
    color: #deba18;
    text-decoration: underline;
  }

  100% {
    opacity: 0.9;
    color: #de1818;
  }
}

.form-control {
  border: 1px solid #ddd;
  box-shadow: none;
  color: #000000;
  height: 40px;
}

.form-control:focus {
  border-color: #e5e1e6;
  box-shadow: none;
  outline: 0 none;
}

.form-control.form-control-sm {
  height: calc(1.5em + 0.5rem + 2px);
}

.form-control.form-control-lg {
  height: calc(1.5em + 1rem + 2px);
}

.btn {
  border-radius: 0;
}

.btn-primary {
  background-color: #de185a;
  border-color: #de185a;
}

.border-primary {
  border: 1px solid #de185a !important;
  color: #de185a;
}

.border-primary:hover {
  background-color: #de185a;
  color: #fff;
}

.btn-primary:hover,
.btn-primary:focus {
  background-color: #c00f4a;
  border-color: #de185a;
  outline: none;
  box-shadow: none;
}

.border-secondary {
  border: 1px solid #353862 !important;
  color: #353862;
}

.border-secondary:hover {
  background-color: #353962;
  color: #fff;
}

.border-voi {
  border: 1px solid #8800d6 !important;
  color: #8800d6;
}

.border-voi:hover {
  background-color: #8800d6;
  color: #fff;
}

.btn-secondary {
  background-color: #353862;
  border-color: #353862;
}

.btn-secondary:hover,
.btn-secondary:focus {
  background-color: #353862;
  border-color: #353862;
  outline: none;
  box-shadow: none;
}

.bg-primary {
  background-color: #de185a !important;
}

.bg-secondary {
  background-color: #373a68 !important;
}

.btn-primary:focus,
.btn-primary:focus {
  outline: none;
  box-shadow: none;
}

.primary:not(:disabled):not(.disabled).active,
.btn-primary:not(:disabled):not(.disabled):active,
.show > .btn-primary.dropdown-toggle,
.btn-primary:not(:disabled):not(.disabled):active:focus {
  background-color: #de185a;
  border-color: #de185a;
  box-shadow: none;
}

.btn-primary-border {
  border: 1px solid #de185a;
  color: #de185a;
}

.btn-primary-border:hover {
  color: #fff;
  background-color: #de185a;
}

.text-primary {
  color: #de185a !important;
}

.btn-save-border {
  border: 1px solid #7012ec;
  color: #7012ec;
}

.btn-save-border:hover {
  background-color: #7012ec;
  color: #fff;
}

.text-secondary {
  color: #353862 !important;
}

