.topbar-section {
  position: fixed;
  background: #01132f;
  padding: 7px 0 0px 0px;
  width: 100%;
  z-index: 11;
  top: 0;
}

.topbar-section::after {
  background-color: #fbb415;
  position: absolute;
  top: 0;
  bottom: -1px;
  right: -63px;
  width: 45%;
  content: "";
  transform: skew(-37deg);
  border-left: 10px solid #39475d;
  z-index: 0;
}

.topbar-section .topbar-inner {
  display: inline-block;
  width: 100%;
  padding: 0;
  vertical-align: middle;
}

.topbar-inner ul li {
  font-size: 14px;
  font-family: "Roboto";
  color: #fff;
  padding: 6px 25px 6px 0px;
  margin-right: 20px;
  margin-bottom: 0;
}

.top-bar-right ul li:last-child {
  padding-right: 0px;
  margin-right: 0px;
}
.view-more-cust {
  width: 100%;
}
.view-more-cust a {
  color: #fff !important;
}
.top-bar-left ul li,
.top-bar-right ul li {
  display: inline-block;
}

.top-bar-right ul li a {
  color: #01132f;
}

.topbar-inner .top-bar-left ul li i {
  font-size: 20px;
  background: #213451;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  padding: 5px;
  text-align: center;
  margin-right: 8px;
}

.top-bar-right {
  position: relative;
  z-index: 1;
}

.top-bar-right ul li {
  color: #01132f;
}

.getstart .dropdown .dropdown-toggle {
  transition: all 0.3s ease 0s;
  background: rgb(14, 40, 70, 1);
  background: -moz-linear-gradient(
    90deg,
    rgba(14, 40, 70, 1) 0%,
    rgba(18, 123, 126, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(14, 40, 70, 1) 0%,
    rgba(18, 123, 126, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(14, 40, 70, 1) 0%,
    rgba(18, 123, 126, 1) 100%
  );
  filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#0e2846", endColorstr="#127b7e", GradientType=1);
  border: 0px;
  border-radius: 50px;
  font-size: 14px;
  padding: 8px 30px;
}

.getstart .dropdown .dropdown-toggle:focus {
  box-shadow: none;
}

.getstart .dropdown .dropdown-toggle:after {
  display: none;
}

.getstart .dropdown .dropdown-menu {
  margin: 0;
  padding: 0;
}

.getstart .dropdown .dropdown-menu li {
  margin: 0;
  padding: 0;
  width: 100%;
}

