}

.menu-toggle {
  position: absolute;
  width: 30px;
  height: 20px;
  padding: 0;
  border: none;
  background-color: transparent;
  right: 20px;
  top: -35px;
}

.menu-toggle span {
  display: block;
  width: 90%;
  height: 2px;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  background-color: #333;
}

.menu-toggle span::before,
.menu-toggle span::after {
  position: absolute;
  left: 0;
  height: 2px;
  content: "";
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  background-color: #333;
}

.menu-toggle span::before {
  top: 0;
  width: 100%;
}

.menu-toggle span::after {
  bottom: 1px;
  width: 80%;
}

