/*-----------------
    5. Mobile Menu
    -----------------------*/

.resmenu-section {
  position: fixed;
  z-index: 99;
  top: 0;
  right: -100%;
  left: auto;
  visibility: hidden;
  width: 390px;
  height: 100%;
  padding-left: 60px;
  transition: all 0.3s ease 0s;
  opacity: 0;
}

.resmenu-section.open {
  right: 0;
  visibility: visible;
  opacity: 1;
}

.resmenu-section.open .resmenu-close {
  visibility: visible;
  opacity: 1;
}

body.resmenu-overlay {
  overflow-y: hidden;
}

body.resmenu-overlay:before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.8);
}

.resmenu-wrap {
  overflow-x: hidden;
  overflow-y: auto;
  height: 100%;
  background-color: #ffffff;
  box-shadow: -5px 0 5px 0 rgba(0, 0, 0, 0.1);
}

.resmenu-wrap .inner {
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  min-height: 100%;
  padding: 40px 30px;
}

.resmenu-close {
  font-size: 24px;
  font-weight: 600;
  line-height: 1;
  position: absolute;
  z-index: 99;
  top: 11px;
  left: 11px;
  visibility: hidden;
  width: 50px;
  height: 50px;
  transition: all 0.3s ease 0s;
  opacity: 0;
  color: #1a77a4;
  border: none;
  background-color: #ffffff;
  box-shadow: -5px 0 5px 0 rgba(0, 0, 0, 0.1);
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.res-menu nav ul {
  font-size: 14px;
  position: static;
  top: 0;
  left: 0;
  visibility: visible;
  width: 100%;
  transition: none;
  opacity: 1;
  background-color: transparent;
  box-shadow: none;
}

.res-menu nav ul li {
  position: relative;
}

.res-menu nav ul li a {
  font-weight: 500;
  line-height: 1;
  display: block;
  padding: 15px 0;
  text-transform: uppercase;
  border-bottom: 1px solid #eeeeee;
}

.res-menu nav ul li:last-child > a {
  border-bottom: none;
}

.res-menu nav ul li .menu-expand {
  line-height: 44px;
  position: absolute;
  z-index: 5;
  top: 0;
  right: 0;
  width: 30px;
  height: 44px;
  cursor: pointer;
  text-align: center;
}

.res-menu nav ul li .menu-expand i {
  position: relative;
  display: block;
  width: 10px;
  margin-top: 22px;
  margin-right: auto;
  margin-left: auto;
  -webkit-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
  transition: all 250ms ease-out;
}

.res-submenu.active a {
  color: #de185a;
}

.res-menu .sub-menu {
  display: none;
  padding: 0;
}

.res-menu .sub-menu li {
  padding: 0;
}

.res-menu .sub-menu li a {
  font-size: 13px;
  text-transform: inherit;
  color: #000;
  padding: 15px;
}

.resmenu-user {
  display: flex;
  -webkit-box-align: center;
  align-items: center;
  margin-bottom: 30px;
}

.resmenu-user a {
  -webkit-box-flex: 1;
  flex-grow: 1;
  padding: 7px;
  text-align: center;
}

